"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { getUser } from "@/lib/auth"
import { TrendingUp, Package, AlertTriangle, Plus, ArrowRight } from "lucide-react"

interface Product {
  id: number
  nom: string
  quantite: number
  unite: string
  prix: number
  dateExpiration: string
  statut: string
}

export default function FarmerDashboardPage() {
  const router = useRouter()
  const [user, setUser] = useState<any>(null)
  const [products, setProducts] = useState<Product[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const currentUser = getUser()
    if (!currentUser || currentUser.role !== "agriculteur") {
      router.push("/login")
      return
    }
    setUser(currentUser)
    fetchProducts()
  }, [router])

  const fetchProducts = async () => {
    try {
      const response = await fetch("http://localhost:8080/api/v1/recoltes")
      const data = await response.json()
      setProducts(data)
    } catch (error) {
      console.error("Erreur lors du chargement des produits:", error)
    } finally {
      setLoading(false)
    }
  }

  const getStats = () => {
    const total = products.length
    const disponibles = products.filter((p) => p.statut === "Disponible").length
    const indisponibles = products.filter((p) => p.statut === "Indisponible").length
    const totalValue = products.reduce((acc, p) => acc + p.prix * p.quantite, 0)
    const expiringProducs = products.filter(
      (p) => new Date(p.dateExpiration) - new Date() < 7 * 24 * 60 * 60 * 1000
    ).length
    return { total, disponibles, indisponibles, totalValue, expiringProducs }
  }

  const stats = getStats()
  const recentProducts = products.slice(0, 5)
  const expiringProducts = products
    .filter((p) => {
      const daysUntilExpiry = Math.floor(
        (new Date(p.dateExpiration).getTime() - new Date().getTime()) / (1000 * 60 * 60 * 24)
      )
      return daysUntilExpiry <= 7 && daysUntilExpiry > 0
    })
    .slice(0, 3)

  if (!user) return null

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 py-8">
        {/* En-t√™te d'accueil */}
        <div className="mb-8">
          <h1 className="text-4xl font-bold text-gray-900">Bienvenue, {user.name || "Agriculteur"}!</h1>
          <p className="text-gray-600 mt-2">G√©rez votre inventaire agricole et suivez vos ventes</p>
        </div>

        {/* Statistiques principales */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-8">
          {/* Total de produits */}
          <Card className="p-6 bg-white shadow-sm hover:shadow-md transition">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-gray-600 text-sm">Total de produits</p>
                <p className="text-3xl font-bold text-gray-900 mt-2">{stats.total}</p>
              </div>
              <Package className="w-12 h-12 text-blue-100 rounded-lg p-2 bg-blue-50" />
            </div>
          </Card>

          {/* Produits disponibles */}
          <Card className="p-6 bg-green-50 shadow-sm hover:shadow-md transition border border-green-200">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-green-700 text-sm">Disponibles</p>
                <p className="text-3xl font-bold text-green-600 mt-2">{stats.disponibles}</p>
              </div>
              <TrendingUp className="w-12 h-12 text-green-100 rounded-lg p-2 bg-green-100" />
            </div>
          </Card>

          {/* Valeur totale */}
          <Card className="p-6 bg-purple-50 shadow-sm hover:shadow-md transition border border-purple-200">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-purple-700 text-sm">Valeur totale</p>
                <p className="text-3xl font-bold text-purple-600 mt-2">{stats.totalValue.toFixed(0)} DH</p>
              </div>
              <TrendingUp className="w-12 h-12 text-purple-100 rounded-lg p-2 bg-purple-100" />
            </div>
          </Card>

          {/* Produits en cours d'expiration */}
          <Card className="p-6 bg-orange-50 shadow-sm hover:shadow-md transition border border-orange-200">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-orange-700 text-sm">En cours d'expiration</p>
                <p className="text-3xl font-bold text-orange-600 mt-2">{stats.expiringProducs}</p>
              </div>
              <AlertTriangle className="w-12 h-12 text-orange-100 rounded-lg p-2 bg-orange-100" />
            </div>
          </Card>
        </div>

        {/* Conteneur principal avec deux colonnes */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8 mb-8">
          {/* Colonne gauche - Actions rapides et produits r√©cents */}
          <div className="lg:col-span-2 space-y-6">
            {/* Actions rapides */}
            <Card className="p-6 shadow-sm">
              <h2 className="text-lg font-semibold text-gray-900 mb-4">Actions rapides</h2>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-3">
                <Link href="/farmer/products/new">
                  <Button className="w-full bg-green-600 hover:bg-green-700 flex items-center justify-center gap-2">
                    <Plus className="w-5 h-5" />
                    Ajouter un produit
                  </Button>
                </Link>
                <Link href="/farmer/products">
                  <Button variant="outline" className="w-full flex items-center justify-center gap-2">
                    <Package className="w-5 h-5" />
                    Voir tous les produits
                  </Button>
                </Link>
                <Button variant="outline" className="w-full flex items-center justify-center gap-2">
                  <ArrowRight className="w-5 h-5" />
                  T√©l√©charger le rapport
                </Button>
              </div>
            </Card>

            {/* Produits en cours d'expiration */}
            {expiringProducts.length > 0 && (
              <Card className="p-6 shadow-sm border-l-4 border-l-orange-500 bg-orange-50">
                <div className="flex items-center gap-3 mb-4">
                  <AlertTriangle className="w-5 h-5 text-orange-600" />
                  <h2 className="text-lg font-semibold text-orange-900">Produits bient√¥t expir√©s</h2>
                </div>
                <div className="space-y-3">
                  {expiringProducts.map((product) => {
                    const daysUntilExpiry = Math.floor(
                      (new Date(product.dateExpiration).getTime() - new Date().getTime()) / (1000 * 60 * 60 * 24)
                    )
                    return (
                      <div key={product.id} className="flex items-center justify-between p-3 bg-white rounded-md">
                        <div>
                          <p className="font-medium text-gray-900">{product.nom}</p>
                          <p className="text-sm text-gray-600">
                            Expire dans {daysUntilExpiry} jour{daysUntilExpiry > 1 ? "s" : ""}
                          </p>
                        </div>
                        <span className="px-3 py-1 bg-orange-100 text-orange-700 rounded-full text-sm font-medium">
                          {product.quantite} {product.unite}
                        </span>
                      </div>
                    )
                  })}
                </div>
              </Card>
            )}
          </div>

          {/* Colonne droite - Produits r√©cents */}
          <div>
            <Card className="p-6 shadow-sm h-full">
              <h2 className="text-lg font-semibold text-gray-900 mb-4">Produits r√©cents</h2>
              {loading ? (
                <div className="text-center py-8">
                  <p className="text-gray-600">Chargement...</p>
                </div>
              ) : recentProducts.length === 0 ? (
                <div className="text-center py-8">
                  <Package className="w-12 h-12 text-gray-300 mx-auto mb-2" />
                  <p className="text-gray-600">Aucun produit</p>
                  <Link href="/farmer/products/new" className="mt-4 inline-block">
                    <Button className="bg-green-600 hover:bg-green-700 text-white">
                      Ajouter le premier
                    </Button>
                  </Link>
                </div>
              ) : (
                <div className="space-y-3">
                  {recentProducts.map((product) => (
                    <div key={product.id} className="p-3 border border-gray-200 rounded-lg hover:bg-gray-50 transition">
                      <h3 className="font-medium text-gray-900 truncate">{product.nom}</h3>
                      <div className="mt-2 space-y-1 text-sm text-gray-600">
                        <p>
                          <span className="font-medium text-gray-700">Qty:</span> {product.quantite} {product.unite}
                        </p>
                        <p>
                          <span className="font-medium text-gray-700">Prix:</span> {product.prix.toFixed(2)} DH
                        </p>
                        <p>
                          <span
                            className={`inline-block px-2 py-0.5 rounded text-xs font-medium ${
                              product.statut === "Disponible"
                                ? "bg-green-100 text-green-800"
                                : "bg-red-100 text-red-800"
                            }`}
                          >
                            {product.statut}
                          </span>
                        </p>
                      </div>
                    </div>
                  ))}
                </div>
              )}
              <Link href="/farmer/products" className="mt-4 block">
                <Button variant="outline" className="w-full">
                  Voir tous les produits
                </Button>
              </Link>
            </Card>
          </div>
        </div>

        {/* Section d'information */}
        <Card className="p-6 bg-blue-50 border border-blue-200 shadow-sm">
          <h2 className="text-lg font-semibold text-blue-900 mb-3">üí° Conseils pour bien d√©marrer</h2>
          <ul className="space-y-2 text-blue-800 text-sm">
            <li>‚Ä¢ Ajoutez vos produits avec les prix et quantit√©s corrects</li>
            <li>‚Ä¢ Mettez √† jour r√©guli√®rement votre stock apr√®s chaque vente</li>
            <li>‚Ä¢ V√©rifiez les produits en cours d'expiration pour √©viter les pertes</li>
            <li>‚Ä¢ Utilisez les filtres et la recherche pour g√©rer efficacement votre inventaire</li>
          </ul>
        </Card>
      </div>
    </div>
  )
}
